import { Component, OnInit, ViewChild, ElementRef, ViewContainerRef, ComponentFactoryResolver, Inject } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { AccountTransactionService, QuickaccessComponent, FileExportService, CurrentUserService } from '../../../shared';
import { AccountTransactionEditComponent } from './account-transaction-edit/account-transaction-edit.component';
import { AccountTransactionAddComponent } from './account-transaction-add/account-transaction-add.component';
import * as pdfMake from 'pdfmake/build/pdfmake';
import * as pdfFonts from 'pdfmake/build/vfs_fonts';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';

@Component({
  selector: 'app-account-transaction',
  templateUrl: './account-transaction.component.html',
  styleUrls: ['./account-transaction.component.scss']
})
export class AccountTransactionComponent implements OnInit {
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('jPanel') jPanel: ElementRef;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @ViewChild('myWindow') myWindow: jqxWindowComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('Insert', { read: ViewContainerRef }) Insert: ViewContainerRef;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  apiUrl: string;
  source: any;
  dataAdapter: any;
  columns: any[];
  editrow: number = -1;

  userData: any = {};

  constructor(
      @Inject('API_URL') apiUrl: string,
      private atService: AccountTransactionService,
      private componentFactoryResolver: ComponentFactoryResolver,
      private fileExport: FileExportService,
      private cus: CurrentUserService,
      private vc: ViewContainerRef) {
      this.apiUrl = apiUrl;
      this.userData = this.cus.getTokenData();
      this.getTable();

  }
  ngOnInit() {
      this.createQuickAccess();
  }

  ngAfterViewInit(): void {
  }

  createQuickAccess() {
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
      this.quickAccess.clear();
      const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }

  getTable() {
      let myUrl = this.apiUrl + 'account_transaction';

      this.source =
          {
              datatype: 'json',
              //   localData: {},
              datafields: [
                  { name: 'id', type: 'string' },
                  { name: 'account_head_id', type: 'number' },
                  { name: 'account_head_name', type: 'string',map:'account_head>name' },
                  { name: 'cost_center_id', type: 'number' },
                  { name: 'cost_center_name', type: 'string',map:'cost_center>name' },
                  { name: 'remarks', type: 'string', },
                  { name: 'value', type: 'number' },
                 
              ],
              id: 'id',
              root: 'data',
              pagesize: 20,
              totalrecords: 100,
              url: myUrl,
              data: this.userData,
              filter: () => {
                  // update the grid and send a request to the server.
                  this.myGrid.updatebounddata('filter');
                },
                sort: () => {
                  // update the grid and send a request to the server.
                  this.myGrid.updatebounddata('sort');
                },
          }

      this.dataAdapter = new jqx.dataAdapter(this.source, {
          formatData: (data) => {
              data['page'] = data['pagenum'] + 1;
              data['limit'] = data['pagesize'];
              return data;
          },
          downloadComplete: (data, status, xhr) => {
              this.source.totalrecords = data.data.total;
              let tableData = data.data.data;
              return tableData;
          }, loadError: (jqXHR, status, error) => {
            let messageDiv: any = document.getElementById('listingMessage');
            messageDiv.innerText = error;
            this.winNotification.open();
            this.jqxLoader.close();
          }
      });

      this.columns = [
          {
              text: 'S.N', sortable: false, filterable: false, editable: false,
              groupable: false, draggable: false, resizable: false,
              datafield: 'id', columntype: 'number', width: 50,
              cellsrenderer: function (row, column, value) {
                  return "<div style='margin:4px;'>" + (value + 1) + "</div>";
              }
          },
          { text: 'Account head name', datafield: 'account_head_id',displayfield:'account_head_name', columntype: 'textbox',filtercondition: 'starts_with' },
          { text: 'Cost center name', datafield: 'cost_center_id',displayfield:'cost_center_name', columntype: 'textbox', filtercondition: 'starts_with' },
          { text: 'Value', datafield: 'value', columntype: 'textbox', filtercondition: 'starts_with' },
          { text: 'Remarks', datafield: 'remarks', columntype: 'textbox', filtercondition: 'starts_with' },
          {
              text: 'Edit', datafield: 'Edit', columntype: 'button',sortable:false,filterable:false, width: 70,
              cellsrenderer: (): string => {
                  return 'Edit';
              },
              buttonclick: (row: number): void => {
                  this.editrow = row;
                  let dataRecord = this.myGrid.getrowdata(this.editrow);
                 // this.myWindow.position({ x: '45%', y: '13%' });
                  this.myWindow.draggable(false);
                  this.myWindow.title('Edit Opening Transaction');
                  this.myWindow.open();
                  const component_factory = this.componentFactoryResolver.resolveComponentFactory(AccountTransactionEditComponent);
                  this.Insert.clear();
                  const dynamic_Component = this.Insert.createComponent(component_factory);
                  (<AccountTransactionEditComponent>dynamic_Component.instance).atEditData = dataRecord;
                  (<AccountTransactionEditComponent>dynamic_Component.instance).UpdatedData.subscribe(result => {
                      if (result.result == true) {
                          //console.log(result.result);
                          let messageDiv = document.getElementById('message');
                          messageDiv.innerText = result['message'];
                          this.msgNotification.open();
                          this.myWindow.hide();
                          this.myGrid.updatebounddata('cells');
                          dynamic_Component.destroy();
                      }else{
                        let messageDiv = document.getElementById('message');
                        messageDiv.innerText = result['message'];
                        this.msgNotification.open();
                       
                      }
                  });

                  (<AccountTransactionEditComponent>dynamic_Component.instance).cancelData.subscribe(result => {
                      if (result == true) {
                          this.myWindow.hide();
                          dynamic_Component.destroy();
                      }
                  });

              }
          }
      ];


  }
  //Get Data after the server side processing as pagination
  rendergridrows = (params: any): any[] => {
      return params.data
  }

  rendertoolbar = (toolbar: any): void => {
      let container = document.createElement('div');
      container.style.margin = '5px';

      let buttonContainer1 = document.createElement('div');
      let buttonContainer2 = document.createElement('div');
      let buttonContainer3 = document.createElement('div');
      let buttonContainer4 = document.createElement('div');
      let buttonContainer5 = document.createElement('div');
      let buttonContainer6 = document.createElement('div');
  
      buttonContainer1.id = 'buttonContainer1';
      buttonContainer2.id = 'buttonContainer2';
      buttonContainer3.id = 'buttonContainer3';
      buttonContainer4.id = 'buttonContainer4';
      buttonContainer5.id = 'buttonContainer5';
      buttonContainer6.id = 'buttonContainer6';
  
      buttonContainer1.style.cssText = 'float: left';
      buttonContainer2.style.cssText = 'float: left; margin-left: 5px';
      buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
      buttonContainer4.style.cssText = 'float: left; margin-left: 5px';
      buttonContainer5.style.cssText = 'float: left; margin-left: 5px';
      buttonContainer6.style.cssText = 'float: left; margin-left: 5px';
  
      container.appendChild(buttonContainer1);
      container.appendChild(buttonContainer3);
      container.appendChild(buttonContainer2);
      container.appendChild(buttonContainer4);
      container.appendChild(buttonContainer5);
      container.appendChild(buttonContainer6);
      toolbar[0].appendChild(container);

      let addRowButton = jqwidgets.createInstance('#buttonContainer1', 'jqxButton', { width: 105, value: 'Add New Row', theme: 'energyblue' });
      let deleteRowButton = jqwidgets.createInstance('#buttonContainer3', 'jqxButton', { width: 150, value: 'Delete Selected Row', theme: 'energyblue' });
      let reloadGridButton = jqwidgets.createInstance('#buttonContainer2', 'jqxButton', { width: 80, value: '<i class="fa fa-refresh fa-fw"></i> Reload', theme: 'energyblue' });
      let exportExcelButton = jqwidgets.createInstance('#buttonContainer4', 'jqxButton', { width: 120, value: '<i class="fa fa-file-excel-o fa-fw"></i> Export Excel', theme: 'energyblue' });
      let exportPdfButton = jqwidgets.createInstance('#buttonContainer5', 'jqxButton', { width: 110, value: '<i class="fa fa-file-pdf-o fa-fw"></i> Export PDF', theme: 'energyblue' });
      let printButton = jqwidgets.createInstance('#buttonContainer6', 'jqxButton', { width: 110, value: '<i class="fa fa-print fa-fw"></i> Print', theme: 'energyblue' });

      addRowButton.addEventHandler('click', () => {
          this.myWindow.draggable(true);
          this.myWindow.title('Add Opening Transaction');
          this.myWindow.open();
          const componentFactory = this.componentFactoryResolver.resolveComponentFactory(AccountTransactionAddComponent);
          this.Insert.clear();
          const dynamic_Component = this.Insert.createComponent(componentFactory);
          (<AccountTransactionAddComponent>dynamic_Component.instance).AddedData.subscribe(result => {
              if (result.result == true) {
                  let messageDiv = document.getElementById('message');
                  messageDiv.innerText = result['message'];
                  this.msgNotification.open();
                  this.myWindow.hide();
                  this.myGrid.updatebounddata('cells');
                  dynamic_Component.destroy();

              }else{
                let messageDiv = document.getElementById('message');
                messageDiv.innerText = result['message'];
                this.msgNotification.open();
               
              }
          });
          (<AccountTransactionAddComponent>dynamic_Component.instance).cancelData.subscribe(result => {
              if (result == true) {
                  this.myWindow.hide();
                  dynamic_Component.destroy();

              }
          });


      })


      deleteRowButton.addEventHandler('click', () => {
        let id = this.myGrid.getselectedrowindexes();
        let ids = [];
        let rowscount = this.myGrid.getdatainformation().rowscount;
        for(let i=0; i<id.length; i++){
          let dataRecord = this.myGrid.getrowdata(Number(id[i]));
          ids.push(dataRecord.id);
        }
        if (ids.length > 0 && ids.length <= parseFloat(rowscount)) {
          if (confirm("Are you sure? You Want to delete")) {
            // let id = this.myGrid.getrowid(selectedrowindex);
             this.jqxLoader.open();            
            this.atService.destroy('['+ids+']').subscribe(data => {
              let messageDiv = document.getElementById('message');
              messageDiv.innerText = data['message'];
              this.msgNotification.open();
              this.jqxLoader.close();
              this.myGrid.updatebounddata('cells');   
            })
          }
        }else{
             let messageDiv = document.getElementById('message');
              messageDiv.innerText = 'Please select some item to delete';
              this.msgNotification.open();
        }
      })

      reloadGridButton.addEventHandler('click', () => {
          this.myGrid.updatebounddata('cells');
        });
    
        exportExcelButton.addEventHandler('click', () => {
          // let currentDate = new Date().toLocaleDateString();
          // let fileName = 'Product-Category-'+ currentDate;
          // this.myGrid.exportdata('xls', 'Product-Category-Master');
          let post: any = {};
          this.atService.index(post)
            .subscribe(
            data => {
              // set items to json response
            //  console.log(data);
              let exportData = data;
              let fileName = this.fileExport.getFileName('account-transaction');
              this.fileExport.downloadFile(exportData, fileName);
            },
            error => {
              console.log(error);
    
            });
        });
    
        exportPdfButton.addEventHandler('click', () => {
          let type:string = 'download';
          this.createPdfFormat(type);
          // this.myGrid.exportdata('pdf', 'Product-Category-Master');
        });
    
        printButton.addEventHandler('click', () => {
          let type:string = 'print';
          this.createPdfFormat(type);
        });

  }; // render toolbar ends here


  createPdfFormat(type: string) {
      let rows = [];
      let post: any = {};
      pdfMake.vfs = pdfFonts.pdfMake.vfs;
  
      this.atService.index(post)
        .subscribe(
        data => {
          // set items to json response
          let exportData = data;
          // Define rows as array to push table rows data.
          let rows = [];
          // Define first row for table to be displayed as header.
          let header = ['Id', 'value', 'remarks Grn no'];
          // Push Firt row into rows array.
          rows.push(header);
          // Format required data & field to be displayed in pdf.
          for (let i = 0; i < exportData.length; i++) {
            // Define a column array for the i(th) row.
            let cols = [];
            cols.push(exportData[i].id);
            cols.push(exportData[i].value);
            cols.push(exportData[i].remarks);
            // Push the columns array ie a single row into rows array.
            rows.push(cols);
          }
  
          // Define PDF Doc
          let dd = {
            pageSize: 'A4',
            content: [
              {
                table: {
                  // headers are automatically repeated if the table spans over multiple pages
                  // you can declare how many rows should be treated as headers
                  headerRows: 1,
                  widths: ['*', '*', '*', '*'],
                  // Set the rows array into body of the table.
                  body: rows
                }
              }
            ],
          };
  
          // createPdf with the provided file name & Download it
          if(type == 'download'){
            let fileName = this.fileExport.getFileName('account-transaction');
            pdfMake.createPdf(dd).download(fileName);
          }else{
            pdfMake.createPdf(dd).print();
          }
          
  
        },
        error => {
          console.log(error);
        });
    }
}



