import { Component, OnInit, Input, Output, Inject, ChangeDetectorRef, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { AccountTransactionService, AccountTransaction, ErrorHandleService, CurrentUserService} from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-account-transaction-edit',
  templateUrl: './account-transaction-edit.component.html',
  styleUrls: ['./account-transaction-edit.component.scss']
})
export class AccountTransactionEditComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('cComboBox') cComboBox: jqxComboBoxComponent;
  @ViewChild('aComboBox') aComboBox: jqxComboBoxComponent;
  @ViewChild('crDrComboBox') crDrComboBox: jqxComboBoxComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Input('atEditData') atEditData;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  account_head: any;
  accountAdapter: any;
  cost_center: any;
  costAdapter: any;
  apiUrl: string;
  atForm: FormGroup;
  EditData: AccountTransaction;

  crDrAdapter = [
    { name: 'CR', id: 'cr' },
    { name: 'DR', id: 'dr' }
  ];

  userData: any = {};

  constructor(
    private fb: FormBuilder,
    private atService: AccountTransactionService,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService,
    @Inject('API_URL') apiUrl: string,
    private cdr: ChangeDetectorRef) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.createForm();
  }
  ngOnInit() {
    this.account_head =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        async: false,
        url: this.apiUrl + 'account_head?limit=all',
      };
    this.accountAdapter = new jqx.dataAdapter(this.account_head, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        this.aComboBox.selectItem(this.atEditData.account_head_id);

      }
    });
    this.cost_center =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        async: false,
        url: this.apiUrl + 'costcenter?limit=all',
      };
    this.costAdapter = new jqx.dataAdapter(this.cost_center, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }, loadComplete: () => {
        this.cComboBox.selectItem(this.atEditData.cost_center_id);

      }
    });

  }
  ngAfterViewInit(): void {
    if (this.atEditData.value && this.atEditData.value < 0) {
      this.crDrComboBox.selectItem('dr');
      this.atEditData['value'] = Math.abs(this.atEditData['value']);
    } else {
      this.crDrComboBox.selectItem('cr');
    }
    this.EditData = new AccountTransaction(this.atEditData);
    this.atForm.setValue(this.EditData);
    this.cdr.detectChanges();
  }
  createForm() {

    this.atForm = this.fb.group({
      'value': [null, Validators.required],
      'remarks': [null, Validators.required],

    });
  }
  saveBtn(post) {
    post['cr_dr'] = this.crDrComboBox && this.crDrComboBox.val();
    post['account_head_id'] = this.aComboBox.val();
    post['cost_center_id'] = this.cComboBox.val();
    this.myValidator.validate(document.getElementById('atForm'));
    if (post['account_head_id'] && post['cost_center_id'] && post['cr_dr']) {
      //  console.log(post);
      this.jqxLoader.open();
      this.atService.update(this.atEditData.id, post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }
  }
  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.remarks', message: 'Remarks is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.value', message: 'Value is required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.account_head_id', message: 'Account head name is required !', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.aComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.cost_center_id', message: 'Cost center name is required !', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.cComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
    ];

}



